body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {

	width: 100%;
	height: 100vh;

}

.Header {
	text-align: center;
	margin:0 ;
	margin-bottom: 30px;
}

.Slider {
	width: 100%;
	height: 100%;
}

.Chart {
	width: 100%;
	height: 100%;
}

.flex {
	display: -ms-flexbox;
	display: flex;
}

.horizontal {
	-ms-flex-direction: row;
	    flex-direction: row;
}

.vertical {
	-ms-flex-direction: column;
	    flex-direction: column;
}

.flex-center {
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.start-center {
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
}

.center-start {
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: start;
	    align-items: flex-start;
}

.center-end {
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: end;
	    align-items: flex-end;
}


.flex-1 {
	-ms-flex: 1 1;
	    flex: 1 1;
}

.flex-2 {
	-ms-flex: 2 1;
	    flex: 2 1;
}

.text-center {
	text-align: center;
}

.active {
	background-color: #222;
	color: #efefef;
	margin: 0 10px;
	padding: 5px 10px;
	border-radius: 5px;
}

input[type="range"] {
    -webkit-transform: rotate(270deg);
    width: 80%;
   
}


